@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,600;1,900&display=swap);
body {
  margin: 0;
  padding-top: 3rem;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  background-color: #CE84AD;
}

p{
  font-weight: 300;
}

div.card-title.h5{
  font-weight: 900 !important;
  color: #CE84AD;
  font-size: 1.5rem;
}

div.h-100.card{
  border: 2px solid #D4CBB3 !important;
}




a.text-light.navbar-brand{
  font-weight: 900;
  margin: .8rem auto;
  font-size: 2.5rem;
}

@media(max-width:375px){
  a.text-light.navbar-brand{
    font-size: 1.7rem;
  }
}

div.d-flex.justify-content-center.container{
   background-color: #CE96A6
}

.emoji-img{
  font-size: 35px;
  text-align: center;
  margin: 1rem auto;
}

body{
  background-color: #D2E0BF !important;
}

div.h-100.card{
  border-color: #D4CBB3;
}
